	module.exports = "<h1 id=split-layout>Split Layout</h1> <h2 id=overview>Overview</h2> <p>Split layouts are meant for use in positioning child elements horizontally, with one of the children being used as primary content area, and the other(s) being used as a secondary content area.</p> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Usage</th> </tr> </thead> <tbody> <tr> <td><code>.pf-l-split</code></td> <td><strong>Outcome:</strong> Initiates the split layout</td> <td><strong>Required:</strong> Yes</td> </tr> <tr> <td><code>.pf-l-split__primary</code></td> <td><strong>Outcome:</strong> Specificies the primary child of the layout, which expands vertucally when needed. The primary child will expand horizontally to fill all space not needed by the secondary children.</td> <td><strong>Required:</strong> No</td> <td><strong>Remarks:</strong> If the primary is not explicitly set, the second child with act as the primary child.</td> </tr> <tr> <td><code>.pf-l-split__secondary</code></td> <td><strong>Outcome:</strong> Specifies a secondary child of the layout, which accommodates to its content.</td> <td><strong>Required:</strong> No <strong>Remarks:</strong> A split can have one or more secondary children. If secondary children are not explicitly set, all children but the second will act as secondary children.</td> </tr> <tr> <td><code>.pf-has-gutter</code></td> <td><strong>Outcome:</strong> Adds space between children</td> <td><strong>Required:</strong> No</td> </tr> </tbody> </table> ";

